const App = (props) => {
  const [user, setUser] = useState(null);

  const set_user = async (id, name, email, image) => {
    await axios.post(props.api + "auth", {id, name, email, image});
    setUser({
      id,
      name,
      email,
      image
    });
  }

  return (user ? <Home api={props.api} user={user} /> : <Auth set_user={set_user} />)
}

export default App;
